import React from 'react';

const NewsItem =(props) => {
        // let { title, description, urlImage, url, author, date, source } = this.props;
        return (
            <>
                <div className="card" style={{backgroundColor: props.mode=== 'light'? 'white' : 'grey'}}>
                    <img src={props.urlImage} className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">{props.title} <span className="position-absolute top-0 translate-middle badge rounded-pill bg-danger" style={{left: '90%', zIndex: '1'}}>
                        {props.source} </span></h5>
                        <p className="card-text">{props.description}</p>
                        <p className="card-text"><small style={{color:'black'}}>By {!props.author ? "Unknown" : props.author} on {new Date(props.date).toUTCString()} </small></p>
                        <a target="_blank" rel="noopener noreferrer" href={props.url} className="btn btn-primary">Read more</a>
                    </div>
                </div>
            </>
        );
}
export default NewsItem
