import './App.css';
import React, {useState}  from 'react';
import Navbar from './Components/Navbar';
import News from './Components/News';
import {
  HashRouter as Router,
  Routes,
  Route,
} from "react-router-dom";
const App = () => {
  // let apiKey = process.env.REACT_APP_NEWS_API
  let apiKey = 'e94c064cad174b949fda95b6f92b6e9b'
  let pageSize = 9;
  const[mode,setMode] = useState('light')
  const theme = () =>{
    if (mode=== 'light'){
      setMode('dark')
      document.body.style.background = 'black'
    }
    if (mode=== 'dark'){
      setMode('light')
      document.body.style.background = 'white'
    }
  }

    return (
      <>
        <Router>
            <Navbar mode={mode} theme={theme}/>
          <Routes>
            <Route path='/' element={<News apiKey={apiKey} mode={mode} pageSize={pageSize} heading='General' country='in' category='general' />}> </Route>
            <Route path='/business' element={<News key="business"  apiKey={apiKey} mode={mode} heading='Business' pageSize={pageSize} country='in' category='business' />}> </Route>
            <Route path='/entertainment' element={<News key="entertainment"  apiKey={apiKey} mode={mode} heading='Entertainment' pageSize={pageSize} country='in' category='entertainment' />}> </Route>
            <Route path='/health' element={<News key="health"  apiKey={apiKey} mode={mode} heading='health' pageSize={pageSize} country='in' category='health' />}> </Route>
            <Route path='/science' element={<News key="science"  apiKey={apiKey} mode={mode} heading='Science' pageSize={pageSize} country='in' category='science' />}> </Route>
            <Route path='/sports' element={<News key="sports"  apiKey={apiKey} mode={mode} heading='Sports'  pageSize={pageSize} country='in' category='sports' />}> </Route>
            <Route path='/technology' element={<News key="technology"  apiKey={apiKey} mode={mode} heading="Technology" pageSize={pageSize} country='in' category='technology' />}> </Route>s
          </Routes>
        </Router>
      </>
    );
}
export default App