import React from 'react';
import { Link } from 'react-router-dom';

const Navbar = (props) => {
  return (
    <>
      <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode} fixed-top`}>
        <div className="container-fluid">
          <Link className="navbar-brand" to="/" style={{ color: props.mode === 'light' ? 'black' : 'bisque' }}>NEWS</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item"><Link className="nav-link" style={{ color: props.mode === 'light' ? 'black' : 'bisque' }} aria-current="page" to="/">Home</Link> </li>
              <li className="nav-item"> <Link className="nav-link" style={{ color: props.mode === 'light' ? 'black' : 'bisque' }} to="/business">Business</Link></li>
              <li className="nav-item"> <Link className="nav-link" style={{ color: props.mode === 'light' ? 'black' : 'bisque' }} to="/entertainment">Entertainment</Link></li>
              <li className="nav-item"> <Link className="nav-link" style={{ color: props.mode === 'light' ? 'black' : 'bisque' }} to="/health">Health</Link></li>
              <li className="nav-item"> <Link className="nav-link" style={{ color: props.mode === 'light' ? 'black' : 'bisque' }} to="/science">Science</Link></li>
              <li className="nav-item"> <Link className="nav-link" style={{ color: props.mode === 'light' ? 'black' : 'bisque' }} to="/sports">Sports</Link></li>
              <li className="nav-item"> <Link className="nav-link" style={{ color: props.mode === 'light' ? 'black' : 'bisque' }} to="/technology">Technology</Link></li>
            </ul>
          </div>
          <div className="form-check form-switch">
            <input className="form-check-input" type="checkbox" role="switch" onClick={props.theme} id="flexSwitchCheckDefault" />
            <label className="form-check-label" htmlFor="flexSwitchCheckDefault" style={{ color: props.mode === 'light' ? 'black' : 'rgba(255,255,255,.55)' }}>Enable {props.mode === 'light' ? 'dark' : 'light'} mode</label>
          </div>
        </div>
      </nav>
    </>
  );
}
export default Navbar