import React, { useEffect, useState } from 'react';
import NewsItem from './NewsItem';
import Spinner from './Spinner';
import PropTypes from 'prop-types';
import InfiniteScroll from "react-infinite-scroll-component";

const News = (props) => {
    const [articles, setArticles] = useState([])
    const [page, setPage] = useState(1)
    const [loading, setLoading] = useState(true)
    const [totalResults, setTotalResults] = useState(0)
    const [searchValue, setSearchValue] = useState(' ')
    const search = (event) => {
        setSearchValue(event.target.value)
        // console.log(event.target.value)
    }
    const updateNews = async () => {
        let url = `https://newsapi.org/v2/top-headlines?category=${props.category}&country=${props.country}&category=business&apiKey=${props.apiKey}&page=${page}&pageSize=${props.pageSize}`;
        setLoading(true)
        let data = await fetch(url)
        let parsedData = await data.json()
        console.log(parsedData)
        setArticles(parsedData.articles)
        setTotalResults(parsedData.totalResults)
        setLoading(false)
    }
    useEffect(() => {
        updateNews();
    }, [])

    const fetchMoreData = async () => {
        setPage(page + 1)
        let url = `https://newsapi.org/v2/top-headlines?category=${props.category}&country=${props.country}&category=business&apiKey=${props.apiKey}&page=${page + 1}&pageSize=${props.pageSize}`
        let data = await fetch(url)
        let parsedData = await data.json()
        setArticles(articles.concat(parsedData.articles))
        setTotalResults(parsedData.totalResults)
    }
    return (
        <>
            <h2 className='text-center' style={{ color: props.mode === 'light' ? 'black' : 'bisque', marginTop: "70px" }}>Top headlines - {props.heading} </h2>
            {loading && <Spinner />}
            <InfiniteScroll
                dataLength={articles.length}
                next={fetchMoreData}
                hasMore={articles.length !== totalResults}
                loader={<Spinner />}
            >
                <div className="container my-3">
                    <form className="d-flex">
                        <input className="form-control me-2" type="search" placeholder="Search" id='searchInput' onChange={search} aria-label="Search" style={{backgroundColor: props.mode==='light' ? 'white' : 'grey', color: props.mode==='light' ? 'black' : 'bisque'}} />
                    </form>
                    <div className="row">
                        {!loading && articles.filter((element)=> element.title.includes(searchValue)).map((element) => {
                            return <div className="col-md-4 mt-4" key={element.url ? element.url : element.urlToImage}>
                                <NewsItem mode={props.mode} title={element.title} description={element.description} urlImage={element.urlToImage} url={element.url} author={element.author} date={element.publishedAt} source={element.source.name} />
                            </div>
                        })}
                    </div>
                </div>
            </InfiniteScroll>
        </>
    );
}
News.defaultProps = {
    pageSize: 8,
    country: 'in',
    category: 'general',
    heading: 'General'
}
News.propTypes = {
    pageSize: PropTypes.number,
    country: PropTypes.string,
    category: PropTypes.string,
    heading: PropTypes.string,
}
export default News